*, **::before, *::after {
  box-sizing: border-box;
  font-family: "Nunito", sans-serif;
}


#list-container {
  width: 400px;
  margin: auto;
}
#list-container ul {
  padding: 0;
  list-style: none;
}
#list-container ul li {
  padding: 5px;
  font-size: 24px;
  background-color: #a8e6cf;
  color: black;
  margin-bottom: 2px;
  overflow: auto;
  height: fit-content;
}
#list-container input {
  width: 100%;
  height: 35px;
}



